import Vue from "vue";
import App from "./App.vue";
import VeeValidate from "vee-validate";
import Toasted from "vue-toasted";

const Options = {
  theme: "primary",
  position: "top-right",
  duration: 5000
};

Vue.use(Toasted, Options);

Vue.use(VeeValidate);

new Vue({
  el: "#app",
  render: h => h(App)
});



