<template>
  <div id="app">
    <div class="container-fluid">
      <div class="row">
        <div id="leftPage" class="leftPage col-md-7 pd-tb-mini pd-r-50">
          <h1 class="text-white text-center">MKOO</h1>
        </div>
        <div style="">
          <div id="rightPage" class="col-lg-5">
					  <div class="rightContent">
              <transition-group name="slide-fade">
                <order-view :key="0" :token="token" v-if="state === 1"  v-on:setSelectedMeal="setSelectedMeal" :mealitems="meals" :details="details" v-on:resetState="resetState"></order-view>
                <details-view :key="1" :token="token" v-show="state === 0" v-on:changeState="changeState" :mealitems="meals"></details-view>
                <meal-view :key="2" v-if="state === 2" v-on:changeState="changeState" :token="token" :method="method" :selectedMeal="selectedMeal" :mealitems="meals" :details="details"></meal-view>
                <thank-you :key="3" v-show="state === 3" v-on:resetState="resetState"></thank-you>
              </transition-group>
            </div>
            <a href="https://kuzapay.com" class="kuzaLink" target="_blank">Powered By <img src="logo.jpg" width="80" alt="KuzaPay"></a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import OrderView from './OrderView';
  import DetailsView from './DetailsView';
  import ThankYou from './ThankYou';
  import MealView from './MealView';
  import Devless from './utils/devless';
  import _ from 'lodash'

  export default {
    name: 'app',
    components: {
      OrderView,
      DetailsView,
      ThankYou,
      MealView
    },
    data() {
      return {
        meals: undefined,
        state: 0,
        method: undefined,
        details: undefined,
        token: undefined,
        selectedMeal: undefined
      }
    },
    methods: {
      changeState(state, obj) {
        this.state = state;
        this.details = obj;
      },
      setSelectedMeal(state, method, selectedMeal, meals) {
        this.state = state;
        this.method = method;
        this.meals = meals;
        this.selectedMeal = selectedMeal
      },
      resetState(state) {
        this.state = state
      }
    },
    created() {
      this.token = window.location.search.substr(9)
    }
  }
</script>

<style>
  .slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>


