<template>
  <div>
    <h3 class="mr-b-20 text-center">Selected Meals (Lunch & Dinner)</h3>
    <hr>
    <div class="table-responsive">
      <table class="table table-striped table-bordered">
        <thead>
          <tr>
            <th>Day</th>
            <th>Lunch</th>
            <th>Dinner</th>
          </tr>
        </thead>
        <tbody>
          <tr v-if="ordered_meals.monday">
            <td>Monday</td>
            <td>{{getLunch(ordered_meals.monday, 'monday')}}</td>
            <td>{{getDinner(ordered_meals.monday, 'monday')}}</td>
          </tr>
          <tr v-if="ordered_meals.tuesday">
            <td>Tuesday</td>
            <td>{{getLunch(ordered_meals.tuesday, 'tuesday')}}</td>
            <td>{{getDinner(ordered_meals.tuesday, 'tuesday')}}</td>
          </tr>
          <tr v-if="ordered_meals.wednesday">
            <td>Wednesday</td>
            <td>{{getLunch(ordered_meals.wednesday, 'wednesday')}}</td>
            <td>{{getDinner(ordered_meals.wednesday, 'wednesday')}}</td>
          </tr>
          <tr v-if="ordered_meals.thursday">
            <td>Thursday</td>
            <td>{{getLunch(ordered_meals.thursday, 'thursday')}}</td>
            <td>{{getDinner(ordered_meals.thursday, 'thursday')}}</td>
          </tr>
          <tr v-if="ordered_meals.friday">
            <td>Friday</td>
            <td>{{getLunch(ordered_meals.friday, 'friday')}}</td>
            <td>{{getDinner(ordered_meals.friday, 'friday')}}</td>
          </tr>
          <tr v-if="ordered_meals.saturday">
            <td>Saturday</td>
            <td>{{getLunch(ordered_meals.saturday, 'saturday')}}</td>
            <td>{{getDinner(ordered_meals.saturday, 'saturday')}}</td>
          </tr>
        </tbody>
      </table>
      <div class="row">
        <div class="col-md-12">
          <textarea name="feedback" v-model="feedback" id="feedback" cols="30" rows="5" class="form-control" placeholder="Please provide your feedback on the menu here"></textarea>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12 mr-t-20 text-center">
          <button class="btn btn-danger" @click="changeState(1)">Edit</button>
          <button id="confirm" class="btn btn-success" @click="submit">Confirm</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import Devless from './utils/devless'

export default {
  props: ['token', 'selectedMeal', 'mealitems', 'details', 'method'],
  data: () => ({
    meals: undefined,
    ordered_meals: {},
    feedback: ''
  }),
  methods: {
    changeState(val) {
      this.$emit('changeState', val, this.details)
    },
    getLunch(lunch, day) {
      let _self = this
      let food;
      _.each(this.mealitems[day], function (d) {
        _.each(lunch, function(v) {
          if(d.mkoo_meal_id === v.mkoo_meal_id && v.type === 'lunch') {
            food = d.related.meal[0].name
          }
        })
      })
      return food
    },
    getDinner(dinner, day) {
      let _self = this
      let food;
      _.each(this.mealitems[day], function (d) {
        _.each(dinner, function(v) {
          if(d.mkoo_meal_id === v.mkoo_meal_id && v.type === 'dinner') {
            food = d.related.meal[0].name
          }
        })
      })
      return food
    },
    async submit() {
      $('button').button('loading')
      let res;
      let orders = this.selectedMeal
      orders.date = ''
      orders.feedback = this.feedback
      if (this.method === 0) {  
        res = await Devless.addData('mkoo', 'place_order', orders)
      } else {
        res = await Devless.addData('mkoo', 'update_order', orders)
      }
      $('button').button('reset')
      if (res.status_code === 609) {
        this.$emit('changeState', 3, this.details)
      } else {
        this.$toasted.error(res.message)
      }
    }
  },
  created() {
    let mm = []
    _.each(this.selectedMeal.ordered_meals, function (v) {
      if (v.mkoo_meal_id !== '') {
        mm.push(v)
      }
    })
    this.ordered_meals = _.groupBy(mm, 'day')
  }
}
</script>



